.dot {
    height: 14px;
    width: 14px;
    background-color: var(--teal);
    border: 1px solid #383838;
    border-radius: 50%;
    display: inline-block;
    margin: 0 2px;
}

.opendot {
    height: 14px;
    width: 14px;
    background-color: transparent;
    border: 1px solid #383838;
    border-radius: 50%;
    display: inline-block;
    margin: 0 2px;
}

